*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --font-color: #fff;
  --hover-logo-color: #ff3700;
  --primary-color: #000;
  --hover-button-color: #d3d3d34d;
  --select-button-color: #d3d3d340;
  --hover-decoration-color: #4b9dfacc;
  --hover-text-color: #f2c655;
  --nav-height: 80px;
  --logo-flex-basis: 100px;
  font-family: Roboto, sans-serif;
}

.company-logo {
  color: var(--font-color);
  flex-basis: var(--logo-flex-basis);
  cursor: default;
  padding-left: 1.5rem;
}

.company-logo:hover {
  color: var(--hover-logo-color);
}

.nav-container {
  background: var(--primary-color);
  height: var(--nav-height);
  flex-direction: row;
  justify-content: left;
  align-items: center;
  width: 100vw;
  display: flex;
  position: fixed;
}

.nav-content-container {
  width: calc(100vw - var(--logo-flex-basis));
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.button {
  height: var(--nav-height);
  background: var(--primary-color);
  color: var(--font-color);
  cursor: pointer;
  border: none;
  justify-content: center;
  align-items: center;
  width: 5rem;
  text-decoration: none;
  display: flex;
  position: relative;
}

.button > span {
  pointer-events: none;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: .5rem;
  display: flex;
}

.button:hover > span {
  background: var(--hover-button-color);
  color: var(--hover-text-color);
}

.button:before {
  content: "";
  background: var(--hover-decoration-color);
  border-radius: 10000000000000000px;
  height: 5px;
  transition: transform .5s;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

.button:hover:before {
  transform: scaleX(1);
}

iframe {
  margin-top: var(--nav-height);
  border: none;
  width: 100%;
}

.current-iframe > span {
  background: var(--select-button-color);
}
